@import 'abstracts/colors';
@import 'abstracts/font';

.technology{
    margin: 5rem 0 8rem;
    padding-left:5vw;

    .title{
        display: inline-flex;
        font-family: $barlow-condensed;
        
        span{
            font-size: clamp(0.9em, 2.5vw, 1.7em);
            font-weight: 700;
            color: $darkGrey;
            letter-spacing: .25rem;
            margin-right: 1rem;
        }

        p{
            font-size: clamp(0.9em, 2.5vw, 1.7em);
            letter-spacing: .25rem;
        }
    }

    .wrapper-content{
        display: flex;
        align-items: center;
        margin-top: 2.5rem;
        height: 515px;

        .img-tech-landscape{
            display: none;
        }

        .contents{
            display: flex;
            width: 85%;

            .nav-tech{
                display: flex;
                flex-direction: column;
                justify-content: center;
                margin-right: 3.5rem;

                li{
                    list-style: none;
                    border-radius: 50%;
                    border: 1px solid $darkGrey;
                    background-color: $black;
                    width: 5rem;
                    height: 5rem;
                    color: $white;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    font-size: 2em;
                    font-family: $bellefair;
                    cursor: pointer;
                    transition: ease-in-out 0.2s;
                }

                li:nth-child(2){
                    margin: 2.5rem 0;
                }

                li:hover{
                    border-color: $white;
                }

                li.activeNavTech{
                    color: $black;
                    background-color: $white;
                }
            }

            .text-tech{
                margin-right: 3rem;

                h3{
                    font-family: $barlow-condensed;
                    font-weight: 300;
                    color: $blue;
                    letter-spacing: 2.7px;
                }

                div{
                    display: flex;
                    position: relative;
                    overflow: hidden;
    
                    h1{
                        font-family: $bellefair;
                        font-size: clamp(1.5em, 5vw, 3.5em);
                        font-weight: 300;
                        margin: 1rem 0;
                        transition: all 0.4s ease-in-out;
                        transform: translateX(0);
                    }
    
                    .non-active-title{
                        transform: translateX(-100%);
                        position: absolute;
                    }
                }

                p{
                    color: $blue;
                    font-size: clamp(0.9em, 5vw, 1.125em);
                    line-height: 2rem;
                    width: 70%;
                }
            }
        }

        .img-tech-portrait{
            width: 50%;
            display: flex;
            position: relative;
              
            img{
                width: 100%;
                height: 100%;
                transition: opacity 0.4s ease-in-out;
            }
        
            .current-img{
                position: relative;
                opacity: 1;
            }
        
            .non-active{
                position: absolute;
                opacity: 0;
                right: 0;
            }
        }
    }
}

@media (max-width: 970px) {
    body{
        padding-left: 0;
    }

    .technology{
        margin: 2.5rem 0 7rem;
        padding: 0;

        .title{
            padding-left: 2rem;
        }
    
        .wrapper-content{
            height: auto;
            flex-direction: column;

            .img-tech-landscape{
                width: 100%;
                display: flex;
                position: relative;
                  
                img{
                    width: 100%;
                    height: 100%;
                    transition: opacity 0.4s ease-in-out;
                }
            
                .current-img{
                    position: relative;
                    opacity: 1;
                }
            
                .non-active{
                    position: absolute;
                    opacity: 0;
                    right: 0;
                }
            }
    
            .contents{
                width: 100%;
                flex-direction: column;
    
                .nav-tech{
                    flex-direction: row;
                    justify-content: center;
                    margin: 3rem 0;
    
                    li{
                        width: 3.75rem;
                        height: 3.75rem;
                        font-size: 1.5em;
                        background-color: unset;
                    }
    
                    li:nth-child(2){
                        margin: 0 1rem;
                    }
                }
    
                .text-tech{
                    margin-right: 0;
                    text-align: center;
    
                    div{
                        justify-content: center;
        
                        h1{
                            margin: 1rem 0;
                        }
        
                        .non-active-title{
                            transform: translateX(-250%);
                        }
                    }
    
                    p{
                        line-height: 1.75rem;
                        font-size: 1em;
                        margin: 0 auto;
                    }
                }
            }
    
            .img-tech-portrait{
                display: none;
            }
        }
    }
}

@media (max-width: 600px){
    .technology{
        .title{
            padding: 0rem;
            width: 100%;
            justify-content: center;
        }
    
        .wrapper-content{
            .contents{    
                .nav-tech{
                    li{
                        width: 2.5rem;
                        height: 2.5rem;
                        font-size: 1em;
                    }
                }
    
                .text-tech{
                    padding: 0 2.5rem;
                    p{
                        line-height: 1.5rem;
                        font-size: 0.9em;
                        margin: 0 auto;
                        width: 100%;
                    }
                }
            }
    
            .img-tech-portrait{
                display: none;
            }
        }
    }
}