.technology {
  margin: 5rem 0 8rem;
  padding-left: 5vw;
}

.technology .title {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  font-family: "Barlow Condensed", sans-serif;
}

.technology .title span {
  font-size: clamp(0.9em, 2.5vw, 1.7em);
  font-weight: 700;
  color: #50535a;
  letter-spacing: .25rem;
  margin-right: 1rem;
}

.technology .title p {
  font-size: clamp(0.9em, 2.5vw, 1.7em);
  letter-spacing: .25rem;
}

.technology .wrapper-content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-top: 2.5rem;
  height: 515px;
}

.technology .wrapper-content .img-tech-landscape {
  display: none;
}

.technology .wrapper-content .contents {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 85%;
}

.technology .wrapper-content .contents .nav-tech {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin-right: 3.5rem;
}

.technology .wrapper-content .contents .nav-tech li {
  list-style: none;
  border-radius: 50%;
  border: 1px solid #50535a;
  background-color: #0B0D17;
  width: 5rem;
  height: 5rem;
  color: #ffffff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 2em;
  font-family: "Bellefair", serif;
  cursor: pointer;
  -webkit-transition: ease-in-out 0.2s;
  transition: ease-in-out 0.2s;
}

.technology .wrapper-content .contents .nav-tech li:nth-child(2) {
  margin: 2.5rem 0;
}

.technology .wrapper-content .contents .nav-tech li:hover {
  border-color: #ffffff;
}

.technology .wrapper-content .contents .nav-tech li.activeNavTech {
  color: #0B0D17;
  background-color: #ffffff;
}

.technology .wrapper-content .contents .text-tech {
  margin-right: 3rem;
}

.technology .wrapper-content .contents .text-tech h3 {
  font-family: "Barlow Condensed", sans-serif;
  font-weight: 300;
  color: #D0D6f9;
  letter-spacing: 2.7px;
}

.technology .wrapper-content .contents .text-tech div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  overflow: hidden;
}

.technology .wrapper-content .contents .text-tech div h1 {
  font-family: "Bellefair", serif;
  font-size: clamp(1.5em, 5vw, 3.5em);
  font-weight: 300;
  margin: 1rem 0;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.technology .wrapper-content .contents .text-tech div .non-active-title {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  position: absolute;
}

.technology .wrapper-content .contents .text-tech p {
  color: #D0D6f9;
  font-size: clamp(0.9em, 5vw, 1.125em);
  line-height: 2rem;
  width: 70%;
}

.technology .wrapper-content .img-tech-portrait {
  width: 50%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
}

.technology .wrapper-content .img-tech-portrait img {
  width: 100%;
  height: 100%;
  -webkit-transition: opacity 0.4s ease-in-out;
  transition: opacity 0.4s ease-in-out;
}

.technology .wrapper-content .img-tech-portrait .current-img {
  position: relative;
  opacity: 1;
}

.technology .wrapper-content .img-tech-portrait .non-active {
  position: absolute;
  opacity: 0;
  right: 0;
}

@media (max-width: 970px) {
  body {
    padding-left: 0;
  }
  .technology {
    margin: 2.5rem 0 7rem;
    padding: 0;
  }
  .technology .title {
    padding-left: 2rem;
  }
  .technology .wrapper-content {
    height: auto;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
  .technology .wrapper-content .img-tech-landscape {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    position: relative;
  }
  .technology .wrapper-content .img-tech-landscape img {
    width: 100%;
    height: 100%;
    -webkit-transition: opacity 0.4s ease-in-out;
    transition: opacity 0.4s ease-in-out;
  }
  .technology .wrapper-content .img-tech-landscape .current-img {
    position: relative;
    opacity: 1;
  }
  .technology .wrapper-content .img-tech-landscape .non-active {
    position: absolute;
    opacity: 0;
    right: 0;
  }
  .technology .wrapper-content .contents {
    width: 100%;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
  .technology .wrapper-content .contents .nav-tech {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    margin: 3rem 0;
  }
  .technology .wrapper-content .contents .nav-tech li {
    width: 3.75rem;
    height: 3.75rem;
    font-size: 1.5em;
    background-color: unset;
  }
  .technology .wrapper-content .contents .nav-tech li:nth-child(2) {
    margin: 0 1rem;
  }
  .technology .wrapper-content .contents .text-tech {
    margin-right: 0;
    text-align: center;
  }
  .technology .wrapper-content .contents .text-tech div {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
  .technology .wrapper-content .contents .text-tech div h1 {
    margin: 1rem 0;
  }
  .technology .wrapper-content .contents .text-tech div .non-active-title {
    -webkit-transform: translateX(-250%);
            transform: translateX(-250%);
  }
  .technology .wrapper-content .contents .text-tech p {
    line-height: 1.75rem;
    font-size: 1em;
    margin: 0 auto;
  }
  .technology .wrapper-content .img-tech-portrait {
    display: none;
  }
}

@media (max-width: 600px) {
  .technology .title {
    padding: 0rem;
    width: 100%;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
  .technology .wrapper-content .contents .nav-tech li {
    width: 2.5rem;
    height: 2.5rem;
    font-size: 1em;
  }
  .technology .wrapper-content .contents .text-tech {
    padding: 0 2.5rem;
  }
  .technology .wrapper-content .contents .text-tech p {
    line-height: 1.5rem;
    font-size: 0.9em;
    margin: 0 auto;
    width: 100%;
  }
  .technology .wrapper-content .img-tech-portrait {
    display: none;
  }
}
