@import 'abstracts/colors';
@import 'abstracts/font';

.home{
    display: flex;
    padding-right: 3rem;
    justify-content: space-between;
    align-items: flex-end;
    max-width:1300px;
    margin: 15rem auto 10rem;
    
    .heading-text{
        width: 50%;
        h3{
            font-family: $barlow-condensed;
            font-weight: 300;
            letter-spacing: .2em;
            color: $blue;
            font-size: clamp(1rem, 2.5vw, 1.7rem);
        }

        h1{
            font-family: $bellefair;
            font-size: clamp(5rem, 15vw, 10rem);
            font-weight: 400;
            margin: 2rem 0;
        }

        p{
            color: $blue;
            line-height: 1.6;
            width: 70%;
        }
    }

    .btn-explore{
        background-color: $white;
        border-radius: 50%;
        width: 17rem;
        height: 17rem;
        color: $black;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 2em;
        font-family: $bellefair;
        cursor: pointer;
        transition: ease-in-out 0.2s;
        text-decoration: none;
    }

    .btn-explore:hover{
        box-shadow: 0px 2px 0px 90px rgba(255,255,255,0.1);
    }
}

@media (max-width: 970px) {
    .home{
        padding-right: 0;
        align-items: center;
        flex-direction: column;
        margin: 5rem auto 0;
        
        .heading-text{
            width: 100%;
            text-align: center;

            h1{
                margin: 2rem 0;
            }

            p{
                width: 100%;
                padding: 0 20vw;
                line-height: 2rem;
            }
        }

        .btn-explore{
            width: 14rem;
            height: 14rem;
            margin: 2.5rem 0;
        }
    }
}

@media (max-width: 600px){
    .home{
        margin-top: 0;
        .heading-text{
            p{
                width: 100%;
                padding: 0.5rem;
                font-size: 0.9em;
            }
        }

        .btn-explore{
            width: 9.3rem;
            height: 9.3rem;
            font-size: 1.25em;
            margin: 2rem 0 20vh;
        }
    }
}