.destination {
  margin: 5rem 0;
  padding: 0 5vw;
}

.destination .title {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  font-family: "Barlow Condensed", sans-serif;
}

.destination .title span {
  font-size: clamp(0.9em, 2.5vw, 1.7em);
  font-weight: 700;
  color: #50535a;
  letter-spacing: .25rem;
  margin-right: 1rem;
}

.destination .title p {
  font-size: clamp(0.9em, 2.5vw, 1.7em);
  letter-spacing: .25rem;
}

.destination .content-destination {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: 5rem;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  gap: 10rem;
}

.destination .content-destination .img-destination {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 40%;
  height: 40%;
  position: relative;
}

.destination .content-destination .img-destination img {
  width: 100%;
  -webkit-transition: opacity 0.4s ease-in-out;
  transition: opacity 0.4s ease-in-out;
}

.destination .content-destination .img-destination .current-img {
  position: relative;
  opacity: 1;
}

.destination .content-destination .img-destination .non-active {
  position: absolute;
  opacity: 0;
}

.destination .content-destination .text-destination {
  width: 50%;
}

.destination .content-destination .text-destination ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  font-family: "Barlow Condensed", sans-serif;
}

.destination .content-destination .text-destination ul li {
  list-style: none;
  margin-right: 2rem;
  cursor: pointer;
  font-size: clamp(0.8em, 2.5vw, 1.2em);
  letter-spacing: 0.2rem;
  -webkit-transition: all .2s;
  transition: all .2s;
  border-bottom: 0px solid rgba(255, 255, 255, 0.7);
  color: #D0D6f9;
}

.destination .content-destination .text-destination ul li:hover {
  padding-bottom: 1rem;
  border-bottom: 2px solid rgba(255, 255, 255, 0.7);
}

.destination .content-destination .text-destination ul li.activeNavigationPlanet {
  padding-bottom: 1rem;
  border-bottom: 2px solid white;
  color: #ffffff;
}

.destination .content-destination .text-destination .title-destination {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  overflow: hidden;
  width: 450px;
}

.destination .content-destination .text-destination .title-destination h1 {
  font-family: "Bellefair", serif;
  font-size: clamp(3em, 10vw, 7em);
  font-weight: 300;
  margin: 2rem 0rem 2rem 0;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.destination .content-destination .text-destination .title-destination .non-active-title {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  position: absolute;
  opacity: 1;
}

.destination .content-destination .text-destination p {
  color: #D0D6f9;
  line-height: 1.6;
  width: 70%;
}

.destination .content-destination .text-destination .info-text {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-top: 1px solid rgba(255, 255, 255, 0.3);
  margin-top: 3rem;
  padding-top: 2rem;
}

.destination .content-destination .text-destination .info-text div {
  width: 50%;
}

.destination .content-destination .text-destination .info-text div h3 {
  font-family: "Barlow Condensed", sans-serif;
  font-weight: 200;
  color: #D0D6f9;
  margin-bottom: 1rem;
}

.destination .content-destination .text-destination .info-text div p {
  color: #ffffff;
  font-family: "Bellefair", serif;
  font-size: clamp(1.4em, 2.5vw, 1.8em);
}

