@import 'abstracts/colors';
@import 'abstracts/font';

*{
    font-size: 16px;
    box-sizing:border-box;
    margin: 0;
    padding: 0;
}

body{
    padding: 3rem 0 0 3rem;
    width: 100vw;
    height:100%;
    overflow-x: hidden;
}

html{
    color: $white;
    font-family: $barlow;
}

@mixin changeBackground($background: url(), $backgroundRepeat: no-repeat, $backgroundCover: cover){
    background: $background;
    background-repeat: $backgroundRepeat;
    background-size: $backgroundCover;
}

.home-body{   
    @include changeBackground(url('../assets/home/background-home-desktop.jpg'));
}

.destination-body{
    @include changeBackground(url('../assets/destination/background-destination-desktop.jpg'));
}

.crew-body{
    @include changeBackground(url('../assets/crew/background-crew-desktop.jpg'));
}

.technology-body{
    @include changeBackground(url('../assets/technology/background-technology-desktop.jpg')); 
}

body{
    background: url('../assets/home/background-home-mobile.jpg');
    background-repeat: no-repeat;
    background-size: cover;
}


@media (max-width: 970px) {
    body{
        padding: 0;
        width: 100vw;
        height: 100%;
    }

    .home-body{   
        @include changeBackground(url('../assets/home/background-home-tablet.jpg'));
    }
    
    .destination-body{
        @include changeBackground(url('../assets/destination/background-destination-tablet.jpg'));
    }
    
    .crew-body{
        @include changeBackground(url('../assets/crew/background-crew-tablet.jpg'));
    }
    
    .technology-body{
        @include changeBackground(url('../assets/technology/background-technology-tablet.jpg')); 
    }
}



@media (max-width: 600px) {
    .home-body{   
        @include changeBackground(url('../assets/home/background-home-mobile.jpg'));
    }
    
    .destination-body{
        @include changeBackground(url('../assets/destination/background-destination-mobile.jpg'));
    }
    
    .crew-body{
        @include changeBackground(url('../assets/crew/background-crew-mobile.jpg'));
    }
    
    .technology-body{
        @include changeBackground(url('../assets/technology/background-technology-mobile.jpg')); 
    }
}