@import 'abstracts/colors';
@import 'abstracts/font';

.crew{
    margin: 5rem 0 8rem;
    padding:0 5vw;
    display: flex;
    height: fit-content;

    .crew-left{
        width: 50%;

        .title{
            display: inline-flex;
            font-family: $barlow-condensed;
            
            span{
                font-size: clamp(0.9em, 2.5vw, 1.7em);
                font-weight: 700;
                color: $darkGrey;
                letter-spacing: .25rem;
                margin-right: 1rem;
            }
    
            p{
                font-size: clamp(0.9em, 2.5vw, 1.7em);
                letter-spacing: .25rem;
            }
        }

        .text-destination{
            margin: 10rem 0 7rem;


            h3{
                font-family: $bellefair;
                font-size: clamp(1em, 5vw, 2em);
                font-weight: 300;
                color: $grey;
            }

            div{
                display: flex;
                position: relative;
                overflow: hidden;
                h1{
                    font-family: $bellefair;
                    font-size: clamp(1.5em, 5vw, 3.5em);
                    font-weight: 300;
                    margin: 1rem 0;
                    transition: all 0.4s ease-in-out;
                    transform: translateX(0);
                }

                .non-active-title{
                    transform: translateX(-100%);
                    position: absolute;
                }
            }


            p{
                color: $blue;
                line-height: 1.6;
                font-size: clamp(0.9em, 5vw, 1.125em);
                width: 70%;
            }
        }

        .nav-crew{
            display: flex;
            li{
                list-style: none;
                width: 1rem;
                height: 1rem;
                background-color: $darkGrey;
                margin: 0 1rem;
                cursor: pointer;
                border-radius: 50%;
                transition:background 0.2s ease-in-out;
            }

            li:nth-child(1){
                margin: 0 1rem 0 0;
            }

            li:hover{
                background-color:rgba(255, 255, 255, 0.7);
            }
    
            li.activeNavCrew{
                background-color: $white;
            }
        }
    }
    

    .img-crew{
        width: 50%;
        display: flex;
        
        div{
            width: 100%;
            display: flex;
            position: relative;
            justify-content: end;
            align-items: flex-end;

            img{
                transition: opacity 0.4s ease-in-out;
                display: block;
                max-width:700px;
                max-height:550px;
                width: auto;
                height: auto;
            }
    
            .current-img{
                position: relative;
                opacity: 1;
            }
    
            .non-active{
                position: absolute;
                opacity: 0;
                right: 0;
            }
        }

        .title{
            display: none;
        }
    }
}

@media (max-width: 970px) {
    .crew{
        height: 100%;
        margin: 2.5rem 0 0;
        flex-direction: column;

        .crew-left{
            width: 100%;

            .text-destination{
                margin: 3rem 0;
                text-align: center;
    
                div{
                    display: flex;
                    justify-content: center;
                    

                    h1{
                        text-align: center;
                    }

                    .non-active-title{
                        transform: translateX(-200%);
                    }
                }

                p{
                    margin: 0 auto;
                }
            }

            .nav-crew{
                justify-content: center;
                li{
                    width: .7rem;
                    height: .7rem;
                    margin: 0 .5rem;
                }

                li:nth-child(1){
                    margin: 0 .5rem 0 0;
                }
            }
        }

        .img-crew{
            width: 100%;
            margin-top: 5rem;
            overflow: hidden;
            div{
                justify-content: center;

                .non-active{
                    right: unset;
                }
            }
        }
    }
}

@media (max-width: 600px){
    .crew{
        margin: 1.5rem 0 3rem;
        flex-direction: column-reverse;

        .crew-left{
            width: 100%;
            display: flex;
            flex-direction: column;
            flex-flow:column-reverse;

            .title{
                display: none;
            }

            .text-destination{
                margin: 3rem 0;
                text-align: center;
    
                p{
                    margin: 0 auto;
                    width: 100%;
                    font-size: 0.9em;
                }
            }

            .nav-crew{
                margin-top: 3rem;
                justify-content: center;
                li{
                    width: .7rem;
                    height: .7rem;
                    margin: 0 .5rem;
                }

                li:nth-child(1){
                    margin: 0 .5rem 0 0;
                }
            }
        }

        .img-crew{
            margin-top: 0;
            align-items: center;
            flex-direction: column;
            border-bottom: 1px solid rgba(255, 255, 255, 0.3);
    
            div{
                width: 50%;
                height: 50%;
                position: relative;

                img{
                    width: 100%;
                    height: 100%;
                }
            }

            .title{
                width: max-content;
                display: flex;
                flex-direction: row;
                font-family: $barlow-condensed;
                margin-bottom: 3rem;
                
                span{
                    font-size: clamp(0.9em, 2.5vw, 1.7em);
                    font-weight: 700;
                    color: $darkGrey;
                    letter-spacing: .25rem;
                    margin-right: 1rem;
                }
        
                p{
                    font-size: clamp(0.9em, 2.5vw, 1.7em);
                    letter-spacing: .25rem;
                }
            }
        }
    }
}