  display: -ms-flexbox;
  display: flex;
  position: relative;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: end;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
}

.crew .img-crew div img {
  -webkit-transition: opacity 0.4s ease-in-out;
  transition: opacity 0.4s ease-in-out;
  display: block;
  max-width: 700px;
  max-height: 550px;
  width: auto;
  height: auto;
}

.crew .img-crew div .current-img {
  position: relative;
  opacity: 1;
}

.crew .img-crew div .non-active {
  position: absolute;
  opacity: 0;
  right: 0;
}

.crew .img-crew .title {
  display: none;
}

