@import 'abstracts/colors';
@import 'abstracts/font';
$active: 2px solid rgb(255, 255, 255);

nav{
    display: flex;
    align-items: center;

    div{
        display: flex;
        align-items: center;
        width: 40%;

        div{
            width: 100%;
            height: 1px;
            margin-left: 2rem;
            background-color: $grey;
        }
    }

    ul{
        width: 60%;
        display: flex;
        padding: 2rem;
        backdrop-filter: blur(10px);
        background-color: rgba(255, 255, 255, 0.1);  

        li{
            list-style: none;
            width: calc(100%/4);
            text-align: center;        

            a{
                text-decoration: none;
                color: $white;
                font-family: $barlow-condensed;
                transition: all .2s;
                border-bottom: 0px solid rgba(255, 255, 255, 0.7);
                letter-spacing: 2.7px;

                span{
                    font-weight: 700;
                }
            }

            a:hover{
                border-bottom: 2px solid rgba(255, 255, 255, 0.7);
                padding-bottom: 2rem;
            }
        }
    }

    .hamburger-icon{
        display: none;
    }
}

.active{
    border-bottom: $active;
    padding-bottom: 2rem;
}

@media (max-width: 970px){
    nav {
        div{
            padding-left: 2rem;
            width: 30%;
            div{
                display: none;
            }
        }

        ul{
            width: 70%;
            .nav-number{
                display: none;
            }
        }
    }

}

@media (max-width: 600px) {
    nav {
        justify-content: space-between;
        padding: 2rem 1rem;

        .logo{
            padding-left: 1rem;
            width: 20%;

            div{
                display: none;
            }
        }

        ul{
            width: 100%;
            inset: 0 0 0 30%;
            display: flex;
            padding: 5rem 2rem;
            position: fixed;
            flex-direction: column;
            z-index: 1;
            transform: translateX(100%);
            transition: ease-in-out 0.2s;

            .nav-number{
                display: inline;
            }

            li{
                text-align: start;    
                width: 100%;    
                margin: 1rem 0;
    
                a{

                    span{
                        margin-right:1rem ;
                        font-weight: 700;
                    }
                }
    
                a:hover{
                    border-bottom: 2px solid rgba(255, 255, 255, 0.7);
                    padding-bottom: 0;
                }
            }
        }

        .hamburger-icon{
            width: 10%;
            padding: 0;
            display: initial;
            z-index: 2;
            position: relative;
            cursor: pointer;
        }
    }

    .active{
        border-bottom: none;
        padding-bottom: 0;
    }

    .activeNavbar{
        transform: translateX(0);
    }
}