nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

nav div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 40%;
}

nav div div {
  width: 100%;
  height: 1px;
  margin-left: 2rem;
  background-color: #979797;
}

nav ul {
  width: 60%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 2rem;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  background-color: rgba(255, 255, 255, 0.1);
}

nav ul li {
  list-style: none;
  width: calc(100%/4);
  text-align: center;
}

nav ul li a {
  text-decoration: none;
  color: #ffffff;
  font-family: "Barlow Condensed", sans-serif;
  -webkit-transition: all .2s;
  transition: all .2s;
  border-bottom: 0px solid rgba(255, 255, 255, 0.7);
  letter-spacing: 2.7px;
}

nav ul li a span {
  font-weight: 700;
}

nav ul li a:hover {
  border-bottom: 2px solid rgba(255, 255, 255, 0.7);
  padding-bottom: 2rem;
}

nav .hamburger-icon {
  display: none;
}

.active {
  border-bottom: 2px solid white;
  padding-bottom: 2rem;
}

@media (max-width: 970px) {
  nav div {
    padding-left: 2rem;
    width: 30%;
  }
  nav div div {
    display: none;
  }
  nav ul {
    width: 70%;
  }
  nav ul .nav-number {
    display: none;
  }
}

@media (max-width: 600px) {
  nav {
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    padding: 2rem 1rem;
  }
  nav .logo {
    padding-left: 1rem;
    width: 20%;
  }
  nav .logo div {
    display: none;
  }
  nav ul {
    width: 100%;
    inset: 0 0 0 30%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 5rem 2rem;
    position: fixed;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    z-index: 1;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    -webkit-transition: ease-in-out 0.2s;
    transition: ease-in-out 0.2s;
  }
  nav ul .nav-number {
    display: inline;
  }
  nav ul li {
    text-align: start;
    width: 100%;
    margin: 1rem 0;
  }
  nav ul li a span {
    margin-right: 1rem;
    font-weight: 700;
  }
  nav ul li a:hover {
    border-bottom: 2px solid rgba(255, 255, 255, 0.7);
    padding-bottom: 0;
  }
  nav .hamburger-icon {
    width: 10%;
    padding: 0;
    display: initial;
    z-index: 2;
    position: relative;
    cursor: pointer;
  }
  .active {
    border-bottom: none;
    padding-bottom: 0;
  }
  .activeNavbar {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}
