.home {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-right: 3rem;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
  max-width: 1300px;
  margin: 15rem auto 10rem;
}

.home .heading-text {
  width: 50%;
}

.home .heading-text h3 {
  font-family: "Barlow Condensed", sans-serif;
  font-weight: 300;
  letter-spacing: .2em;
  color: #D0D6f9;
  font-size: clamp(1rem, 2.5vw, 1.7rem);
}

.home .heading-text h1 {
  font-family: "Bellefair", serif;
  font-size: clamp(5rem, 15vw, 10rem);
  font-weight: 400;
  margin: 2rem 0;
}

.home .heading-text p {
  color: #D0D6f9;
  line-height: 1.6;
  width: 70%;
}

.home .btn-explore {
  background-color: #ffffff;
  border-radius: 50%;
  width: 17rem;
  height: 17rem;
  color: #0B0D17;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 2em;
  font-family: "Bellefair", serif;
  cursor: pointer;
  -webkit-transition: ease-in-out 0.2s;
  transition: ease-in-out 0.2s;
  text-decoration: none;
}

.home .btn-explore:hover {
  -webkit-box-shadow: 0px 2px 0px 90px rgba(255, 255, 255, 0.1);
          box-shadow: 0px 2px 0px 90px rgba(255, 255, 255, 0.1);
}

@media (max-width: 970px) {
  .home {
    padding-right: 0;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    margin: 5rem auto 0;
  }
  .home .heading-text {
    width: 100%;
    text-align: center;
  }
  .home .heading-text h1 {
    margin: 2rem 0;
  }
  .home .heading-text p {
    width: 100%;
    padding: 0 20vw;
    line-height: 2rem;
  }
  .home .btn-explore {
    width: 14rem;
    height: 14rem;
    margin: 2.5rem 0;
  }
}

@media (max-width: 600px) {
  .home {
    margin-top: 0;
  }
  .home .heading-text p {
    width: 100%;
    padding: 0.5rem;
    font-size: 0.9em;
  }
  .home .btn-explore {
    width: 9.3rem;
    height: 9.3rem;
    font-size: 1.25em;
    margin: 2rem 0 20vh;
  }
}
