@import 'abstracts/colors';
@import 'abstracts/font';
$activeNavigatePlanet: 3px solid rgb(255, 255, 255);

.destination{
    margin: 5rem 0;
    padding:0 5vw;

    .title{
        display: inline-flex;
        font-family: $barlow-condensed;
    
        span{
            font-size: clamp(0.9em, 2.5vw, 1.7em);
            font-weight: 700;
            color: $darkGrey;
            letter-spacing: .25rem;
            margin-right: 1rem;
        }
    
        p{
            font-size: clamp(0.9em, 2.5vw, 1.7em);
            letter-spacing: .25rem;
        }
    }

    .content-destination{
        display: flex;
        margin-top: 5rem;
        justify-content: center;
        align-items: center;
        gap: 10rem;

        .img-destination{
            display: flex;
            justify-content: center;
            width: 40%;
            height: 40%;
            position: relative;

            img{
                width: 100%;
                transition: opacity 0.4s ease-in-out;
            }

            .current-img{
                position: relative;
                opacity: 1;
            }

            .non-active{
                position: absolute;
                opacity: 0;
            }
        }

        .text-destination{
            width: 50%;
            ul{
                display: flex;
                width: 100%;
                font-family: $barlow-condensed;

                li{
                    list-style: none;
                    margin-right: 2rem;
                    cursor: pointer;
                    font-size: clamp(0.8em, 2.5vw, 1.2em);
                    letter-spacing: 0.2rem;
                    transition: all .2s;
                    border-bottom: 0px solid rgba(255, 255, 255, 0.7);
                    color: $blue;
                }

                li:hover{
                    padding-bottom: 1rem;
                    border-bottom: 2px solid rgba(255, 255, 255, 0.7);
                }

                li.activeNavigationPlanet{
                    padding-bottom: 1rem;
                    border-bottom: 2px solid rgba(255, 255, 255, 1);
                    color: $white;
                }
            }

            .title-destination{
                display: flex;
                position: relative;
                overflow: hidden;
                width: 450px;

                h1{
                    font-family: $bellefair;
                    font-size: clamp(3em, 10vw, 7em);
                    font-weight: 300;
                    margin: 2rem 0rem 2rem 0;
                    transition: all 0.4s;
                    transform: translateX(0);
                }

                .non-active-title{
                    transform: translateX(-100%);
                    position: absolute;
                    opacity: 1;
                }
            }

            p{
                color: $blue;
                line-height: 1.6;
                width: 70%;
            }
            
            .info-text{
                display: flex;
                border-top: 1px solid rgba(255, 255, 255, 0.3);
                margin-top: 3rem;
                padding-top: 2rem;

                div{
                    width: 50%;
                    h3{
                        font-family: $barlow-condensed;
                        font-weight: 200;
                        color: $blue;
                        margin-bottom: 1rem;
                    }
    
                    p{
                        color: $white;
                        font-family: $bellefair;
                        font-size: clamp(1.4em, 2.5vw, 1.8em);
                    }
                }
            }
        }
    }

}

@media (max-width: 970px) {
    .destination{
        height: 1024px;
        .content-destination{
            flex-direction: column;
            gap: 2.5rem;

            .img-destination{
                width: 50%;
                height: 50%;
            }

            .text-destination{
                width: 85%;
                display: flex;
                flex-direction: column;

                ul{
                    width: fit-content;
                    margin: 0 auto;
                    display: flex;
                    font-family: $barlow-condensed;
    
                    li{
                        margin: 0;
                    }

                    li:nth-child(2), li:nth-child(3){
                        margin: 0 1rem;
                    }

                    li:nth-child(3){
                        margin:0 1rem 0 0;
                    }
    
                    li:hover{
                        padding-bottom: 1rem;
                        border-bottom: 2px solid rgba(255, 255, 255, 0.7);
                    }
    
                    li.activeNavigationPlanet{
                        padding-bottom: 1rem;
                        border-bottom: 2px solid rgba(255, 255, 255, 1);
                        color: $white;
                    }
                }

                .title-destination{
                    display: initial;
                    width: 100%;
                    overflow:hidden;
                    margin: 0 auto;

                    h1{
                        text-align: center;
                    }
                }
    
                p{
                    width: 100%;
                    text-align: center;
                }
                
                .info-text{
                    width: 100%;    
                    div{
                        text-align: center;
                        h3{
                            margin-bottom: 0.5rem;
                        }
                    }
                }
            }
    
        }

    }
}

@media (max-width: 600px){
    .destination{
        height: fit-content;
        margin: 1rem 0 2rem;
        display: flex;
        flex-direction: column;
        align-items: center;

        .content-destination{
            margin-top: 2.5rem;

            .text-destination{
                p{
                    font-size: 0.8em;
                }
                
                .info-text{
                    flex-direction: column;
                    align-items: center;
                    gap: 1rem;
                }
            }
    
        }

    }
} 